.header {
  height: 160px;
  padding-top: 20px;
}

/* header */
.header .top-wrapper {
  position: relative;
}
.header .top-wrapper .back-wrapper {
  width: 50px;
  height: 31px;

  position: absolute;
  left: 0;
  top: 0;
  text-align: center;
  line-height: 31px;
}
.header .top-wrapper .back-wrapper span {
  display: inline-block;
  color: #fff;
}
.header .top-wrapper .search-wrapper {
  width: 100%;
  height: 31px;
  /* background: purple; */
  padding: 0 104px 0 50px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.header .top-wrapper .search-wrapper .search-icon {
  margin-left: 10px;
  margin-top: 9px;
  width: 28px;
  height: 31px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 13px 13px;
  position: absolute;
}
.header .top-wrapper .search-wrapper .search-bar {
  width: 100%;
  height: 31px;
  border: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #cdcdcc;
  border-radius: 25px;
  padding-left: 28px;
  outline: none;
}
.header .top-wrapper .more-wrapper {
  width: 65px;
  height: 24px;

  position: absolute;
  right: 0;
  top: 0;
  padding: 7px 15px 0 24px;
}
.header .top-wrapper .more-wrapper .spelling-bt {
  width: 30px;
  height: 17px;
  color: white;
  line-height: 17px;
  border: 1px solid white;
  text-align: center;
  float: left;
  text-decoration: none;
  font-size: 10px;
}
.header .top-wrapper .more-wrapper .more-bt {
  float: right;
  width: 30px;
  height: 31px;
  margin-top: 6px;
}
.header .top-wrapper .more-wrapper .more-bt .s-radius {
  width: 3px;
  height: 3px;
  border-radius: 50%;
  border: 1px solid white;
  display: block;
  float: left;
  margin-right: 1px;
}
.header .bg-wrapper {
  width: 100%;
  height: 150px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  background-size: 100% 135%;
  background-position: center -12px;
}

.nav[data-v-5c73da7c] {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #e4e4e4;
}
.nav-item[data-v-5c73da7c] {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  font-size: 13px;
  text-decoration: none;
  color: #666666;
}
.nav .active[data-v-5c73da7c] {
  color: #ffbb22;
}
.nav .active .line[data-v-5c73da7c] {
  width: 21px;
  height: 2px;
  display: inline-block;
  background: #ffbb22;
  position: absolute;
  left: 50%;
  bottom: 0;
  margin-left: -10px;
}




































